@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;400;500;600;700;800;900&display=swap);
*{
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Type Scale */

html {font-size: 100%;} /*16px*/

body {
  --font-one: 'Montserrat Alternates';
  --font-two: 'Montserrat';

  font-family: var(--font-one);
  font-weight: 400;
  line-height: 1.5;
  color: #000000;

  --text-big3: 5.96rem;
  --text-big2: 4.768rem;
  --text-big1: 3.815rem;
}

h1, h2, h3, h4, h5 {
  font-family: var(--font-one), var(--font-two);
  font-weight: 500;
  line-height: 1.3;
}

h1 {font-size: 3.052rem;}

h2 {font-size: 2.441rem;}

h3 {font-size: 1.953rem;}

h4 {font-size: 1.563rem;}

h5 {font-size: 1.25rem;}

small, .text_small {font-size: 0.8rem;}

a{ text-decoration: none; }


/* Color Scheme */

body{
  /* Colors */
  --black-fogra: #0A1E27;
  --dark-black-fogra: #051116;
  --bright-yellow: #FCA61D;
  --princeton-orange: #F5812A;
  --cinnabar: #E64D2D;
  --floral-white: #FFFCF7;
}

/* List Style */
ul{
  list-style: none;
}

/* Buttons */
button{
  outline: none;
  border: none;
}
/* background: linear-gradient(to bottom right, #333 25%, #fff 0%); */
/* ===== Home ===== */
#home{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: var(--floral-white);
  

  --home-p-size: 16px;
  --home-h1-size: 80px;
  --home-content-height: 250px;
}
.home__nav,
.home__content,
.home__socials{
  z-index: 10;
}

/* ==== Top Nav Links ==== */
.home__nav{
  position: absolute;
  right: 10px;
  top: 4vh;
}
.home__nav ul{
  display: -webkit-flex;
  display: flex;
}
.home__nav li{
  margin: 0 15px;
}
.home__nav a{
  position: relative;
  padding-bottom: 5px;
  letter-spacing: 2px;
  font-weight: 700;
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
  cursor: pointer;
  transition: .2s;
}
.home__nav a:hover{ 
  color: var(--princeton-orange);
}

/* ==== Social Links ==== */
.home__socials{
  position: absolute;
  right: 20px;
  top: auto;
  bottom: auto;
  height: var(--home-content-height);
}
.home__socials li{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
}
.home__socials .fab{
  font-size: calc(var(--home-p-size) + 14px);
  color: var(--dark-black-fogra);
}
.home__socials .fa-instagram{
  font-weight: bold;
}
.home__socials a,
.home__socials i{
  line-height: 1.3;
  transition: .25s;
}
.home__socials a:hover, 
.home__socials a:hover i{ 
  color: var(--princeton-orange);
}

/* ==== Content ==== */
.home__content{
  position: absolute;
  height: var(--home-content-height);
}
.home__content h1{
  display: -webkit-flex;
  display: flex;
  letter-spacing: 5px;
  font-size: var(--home-h1-size);
  color: var(--dark-black-fogra);
}
.home__content h1 span{
  margin: 0 20px;
}
.home__content p{
  letter-spacing: 5px;
  font-weight: bold;
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
}
/* Button Animation */
.home__content button{
  margin: 20px 0px;
  padding: 8px 10px;
  letter-spacing: 1px;
  font-weight: bold;
  font-family: inherit;
  font-size: var(--home-p-size);
  color: var(--dark-black-fogra);
  border-radius: 5px;
  border: 2px solid var(--dark-black-fogra);
  background: var(--bright-yellow);
  transition: .25s;
  cursor: pointer;
}
.home__content button i{
  padding: 0px 8px;
  font-size: 16px;
}
.home__content button:hover{
  color: var(--floral-white);
  background: var(--dark-black-fogra);
}



/* ==== Media Queries ==== */

/* Laptop */
@media screen and (max-width: 1024px){
  .home__content h1{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .home__content h1 span{
    margin: 0;
  }
}
/* Tablet */
@media screen and (max-width: 768px){
  .home__nav,
  .home__socials,
  .home__content button i{
    display: none;
  }
  .home__content{
    --home-h1-size: 70px;
  }
}
/* Mobile L */
@media screen and (max-width: 425px){
  .home__content{ 
    width: 60vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-left: 20px;
    --home-h1-size: 14.5vw;
    --home-p-size: 4.5vw;
  }
}
/* Mobile S */
@media screen and (max-width: 375px){
  .home__content button i{ 
    display: none;
  }
}
/* Retro Lines Decoration */
.retroLines{
  position: relative;
  height: 100%;
  width: 100%;

  --border-radius: 18vh;
  --line-width: 1.75vh;
}
.retroLines__black{
  position: absolute;
  left: 0;
  bottom: 0;
  height: 100%; /* Height of black lines */
  width: 100%;
}
.retroLines__red,
.retroLines__orange,
.retroLines__yellow{
  position: absolute;
  left: 0; /* Space in between colored lines */
  bottom: 0;
  height: 90%; /* Height of colored lines */
  width: 100%;
}

/* Line Colors */
.retroLines__black{ background: var(--dark-black-fogra); }
.retroLines__red{ background: var(--cinnabar); }
.retroLines__orange{ background: var(--princeton-orange); }
.retroLines__yellow{ background: var(--bright-yellow); }

/* Inside white space - creates curved line effect */
.retroLines__blackInside,
.retroLines__redInside,
.retroLines__orangeInside,
.retroLines__yellowInside{
  position: absolute;
  left: var(--line-width); /* Vertical colored line width */
  top: -1px;
  height: calc(100% - var(--line-width)); /* Horizontal colored line height */
  width: 100%;
  background: var(--floral-white); /* Background color */
  overflow: hidden;
}

/* Border Radius */
/* Outer most radius[180px] - ( line width * inside border # ) */
.retroLines__blackInside{ 
  border-bottom-left-radius: var(--border-radius); 
}
.retroLines__redInside{ 
  border-bottom-left-radius: calc(var(--border-radius) - 1.75vh)
}
.retroLines__orangeInside{ 
  border-bottom-left-radius: calc(var(--border-radius) - 3.5vh) 
}
.retroLines__yellowInside{ 
  border-bottom-left-radius: calc(var(--border-radius) - 5.25vh) 
}


@media screen and (max-width: 425px) {
  .retroLines{ 
    --line-width: 1.25vh; 
    --border-radius: 15vh;
  }
  .retroLines__blackInside{ 
    border-bottom-left-radius: var(--border-radius); 
  }
  .retroLines__redInside{ 
    border-bottom-left-radius: calc(var(--border-radius) - 1.25vh)
  }
  .retroLines__orangeInside{ 
    border-bottom-left-radius: calc(var(--border-radius) - 2.5vh) 
  }
  .retroLines__yellowInside{ 
    border-bottom-left-radius: calc(var(--border-radius) - 3.75vh) 
  }
}
.burger{
    z-index: 20;
    display: none;
    position: absolute;
    top: 4vh;
    right: 30px;
    height: 25px;
    width: 25px;
    cursor: pointer;
    -webkit-appearance: none;
}
.burger span{
    position: absolute;
    top: 45%;
    height: 3px;
    width: 25px;
}
.burger span::before,
.burger span::after{
    content: '';
    position: absolute;
    height: 3px;
    width: 25px;
}
.burger span,
.burger span::before,
.burger span::after{ 
    transition: .35s;
    background: var(--dark-black-fogra); 
}
.burger span::before{
    top: -.6rem;
}
.burger span::after{
    top: .6rem;
}

/* Hover Effect */
.burger:hover span{
    background: var(--princeton-orange);
}
.burger:hover span::before{
    background: var(--bright-yellow);
}
.burger:hover span::after{ 
    background: var(--cinnabar); 
}

/* Small Fixes */

/* Cant see yellow ::before on yellow background */
.burger-active.burger:hover span::before{
    background: var(--cinnabar);
}
/* Make burger follow if scrolled */
.burger-active.burger{
    position: fixed;
}

/* Burger Animation */
.burger-active.burger span{
    background: rgba(0, 0, 0, 0);
}
.burger-active.burger span::before{
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.burger-active.burger span::after{
    top: 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);  
}

@media screen and (max-width: 768px){
    .burger{
        display: inline;
        display: initial;
    }
}
.sidebar{
  z-index: 15;
  display: none;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  position: fixed;
  top: 0;
  right: -360px;
  height: 100vh;
  width: 350px;
  background-color: var(--bright-yellow);
  transition: .5s ease;
}
.sidebar-active{ right: 0; }

/* Navigation + Resume */
.sidebar__nav{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 20vh;
  margin-bottom: 10vh;
}
.sidebar__nav a{ 
  cursor: pointer;
}

/* Resume Button */
.sidebar__resume{
  margin-bottom: 10vh;
  cursor: pointer;
}

/* Links */
.sidebar__links{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 50px;
  width: 50%;
  margin-bottom: 20vh;
}
.sidebar__links li{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.sidebar__links li a{
  padding: 5px 5px;
  cursor: pointer;
}

/* Font sizes */
.sidebar i{ font-size: 35px; } /* Icons */
.sidebar a { font-size: 25px; } /* Text */

/* Hovers: nav links */
.sidebar__nav a:hover,
.sidebar__resume:hover{
  letter-spacing: 2px;
}
/* Hovers: socials */
.sidebar__links a:hover{
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.sidebar a{ transition: .2s;}


/* ==== Media Queries ==== */

/* Tablet */
@media screen and (max-width: 768px) {
  .sidebar{
    display: -webkit-flex;
    display: flex;
  }
}
/* Mobile L - S */
@media screen and (max-width: 425px) {
  .sidebar{
    width: 100%;
    right: -100%;
  }
  .sidebar-active{
    right: 0;
  }
  .sidebar__nav{
    height: 25vh;
  }
}

